.mkdf-blog-holder.mkdf-blog-single {

    @include ipad-landscape {
        article {

            .mkdf-like-posts-enabled & {
                .mkdf-post-text-main {
                    width: 100%;
                }

                .mkdf-post-info-top {
                    width: 100%;

                    .mkdf-social-share-holder {
                        margin-bottom: 20px;
                        &.mkdf-list li {
                            display: inline-block;
                            border: 1px solid $default-border-color;
                            border-right: none;

                            &:last-child {
                                border-right: 1px solid $default-border-color;
                                margin: 0;
                            }
                        }

                    }

                }
            }
        }
    }

    @include ipad-portrait {
    
        article {

            .mkdf-post-info-bottom {
            
                .mkdf-post-info-bottom-left,
                .mkdf-post-info-bottom-right {
                    width: 100%;
                    float: none;
                }
            
                .mkdf-post-info-bottom-right {
                    text-align: inherit;
                    margin: 10px 0 0;
                }
            }
        }
    }

    @include phone-portrait {
        article {
            .mkdf-post-heading {
                margin-bottom: 38px;
            }

            .mkdf-post-text-main {
                .mkdf-post-title {
                    font-size: 30px;
                }
            }

            .mkdf-post-info-bottom {

                .mkdf-post-info-bottom-right {
                    >div:last-child {
                        margin-left: 0;
                    }
                }
            }

        }
    }
}
