/* ==========================================================================
   Blog List - Standard style responsive- begin
   ========================================================================== */

@include ipad-portrait {
  .mkdf-blog-holder {

      &.mkdf-blog-standard {

          article {

              .mkdf-like-posts-enabled & {

                  .mkdf-post-info-left {
                      width: 90px;
                  }

                  .mkdf-post-text-inner {
                      width: calc(100% - 90px);
                  }
              }

             .mkdf-post-title {
                padding-right: 0;
                font-size: 35px;
             }
          }
      }
  }

}

@include phone-portrait {
    .mkdf-blog-holder {

        &.mkdf-blog-standard {

            article {

                .mkdf-like-posts-enabled & {

                    .mkdf-post-info-left {
                        width: 100%;
                    }

                    .mkdf-post-text-inner {
                        width: 100%;
                    }

                }

                .mkdf-post-title {
                    font-size: 28px;
                }

                &.format-link {

                    .mkdf-post-title {
                        padding: 10px 10px 0 10px;
                    }

                    .mkdf-post-mark {
                        margin-top: -10px;
                    }
                }

                &.format-quote {
                    .mkdf-post-quote-holder-inner {
                        padding: 10px 10px 0 10px;
                    }

                    .mkdf-post-mark {
                        margin-top: -25px;
                    }
                }
            }
        }
    }

}



/* ==========================================================================
   Blog List - Standard style responsive- end
   ========================================================================== */